header{
    background: url(../img/header_back.png);
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
    height: 535px;
    padding-top: 75px;
}
.evart-radio{
    background: url(../img/radio_back.png);
    width: 100%;
    height: 403px;
    border-radius: 10px;
    box-shadow: inset 0px 2px 0px 0px rgba(255, 255, 255, 0.004);
    border: 1px solid rgba(212, 212, 212, 0.65);
    display: flex;

    .evart-dock{
        height: 100%;
        display: flex;
        flex-flow: column wrap;
        align-items: center;

    }
    .evart-panel{
       flex: 7;
        .evart-dots{
            background-image: url(../img/dots.png);
            width: 323px;
            height: 18px;
        }

        .evart-display {
            width: 100%;
            flex: 3;
            position: relative;
            border: 5px solid transparent;
            display: flex;
            flex-flow: column wrap;
            &:before{
                content: '';
                background: linear-gradient(to top, #ffffff 5%, #494949 50%, #ffffff 52%, #d9d9d9 100%);
                position: absolute;
                top: -5px;
                bottom: -5px;
                left: -5px;
                right: -5px;
                border-radius: 10px;
            }
            &:after{
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                border-radius: 10px;
                background-image: url(../img/display.png);
                background-size: cover;
            }
            .evart-logo{
                background-image: url(../img/logo.png);
                align-self: flex-end;
                z-index: 1;
                width: 100%;
            }
        }

        .evart-controls{
            width: 100%;
            flex: 1;
        }
    }
    .evart-playlist{
        flex: 3;
    }
}





